/* Global Styles */
body {
	margin: 0;
	padding: 0;
	font-family: 'Lobster', cursive;
	background: linear-gradient(135deg, #ff9a9e, #fad0c4);
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
  }

  /* Start Page */
  #start-page {
	display: flex;
	gap: 20px;
	flex-wrap: wrap;
	justify-content: center;
  }

  .tile {
	background: rgba(255, 255, 255, 0.85);
	border-radius: 10px;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
	padding: 60px 80px;
	cursor: pointer;
	text-align: center;
	transition: transform 0.2s;
	min-width: 250px;
  }

  .tile:hover {
	transform: scale(1.05);
  }

  /* Back Button placed at top-right corner and made larger */
  .back-button {
	position: fixed;
	top: 20px;
	right: 20px;
	z-index: 1000;
	background: #fff;
	border: 2px solid #e63946;
	border-radius: 5px;
	padding: 10px 20px;
	cursor: pointer;
	font-size: 1.5em;
  }

  /* Valentine Page */
  #valentine-page {
	text-align: center;
	background: rgba(255, 255, 255, 0.85);
	padding: 30px 40px;
	border-radius: 15px;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
	max-width: 90%;
  }

  #valentine-page h1 {
	font-size: 2.5em;
	margin-bottom: 30px;
	color: #e63946;
  }

  #valentine-buttons {
	position: relative;
	display: inline-block;
  }

  /* Valentine Buttons */
  .valentine-btn {
	font-size: 1.5em;
	padding: 10px 25px;
	border: none;
	border-radius: 10px;
	cursor: pointer;
	transition: background-color 0.2s ease;
	position: absolute;
	top: 0;
  }

  #tak {
	background-color: #e63946;
	color: #fff;
	left: 0;
	transform-origin: left center;
	z-index: 100;
  }

  #tak:hover {
	background-color: #d62828;
  }

  #nie {
	background-color: #6c757d;
	color: #fff;
	right: 0;
  }

  #nie:hover {
	background-color: #5a6268;
  }

  /* Highlight Animation for Valentine "Tak" Button */
  @keyframes highlightTak {
	0% {
	  transform: scale(1);
	  background-color: #e63946;
	}
	25% {
	  transform: scale(1.2);
	  background-color: #f06d70;
	}
	50% {
	  transform: scale(1);
	  background-color: #e63946;
	}
	75% {
	  transform: scale(1.2);
	  background-color: #f06d70;
	}
	100% {
	  transform: scale(1);
	  background-color: #e63946;
	}
  }
  .highlight {
	animation: highlightTak 1s ease-in-out;
  }

  /* Wiersz (Love Letter) Page */
  #wiersz-page {
	text-align: center;
	background: #fff0f5;
	border: 2px dashed #e63946;
	border-radius: 15px;
	max-width: 90%;
	position: relative;
	margin: 50px 0; /* 50px top and bottom margins */
	/* Fit the container within the viewport minus margins */
	height: calc(100vh - 255px);
	display: flex;
	flex-direction: column;
	box-sizing: border-box;
	overflow: hidden;
	padding: 20px;
	width: min(98vw, 500px);
	margin-left: auto;
    margin-right: auto;
  }

  #wiersz-page h2 {
	font-size: 2em;
	margin-bottom: 20px;
	color: #e63946;
  }

  /* Envelope inside Wiersz page fills remaining space */
  #wiersz-page .envelope {
	flex: 1;
	display: flex;
	flex-direction: column;
	background: #ffe6f0;
	border: 2px solid #e63946;
	border-radius: 10px;
	padding: 20px;
	position: relative;
	box-sizing: border-box;
	overflow: hidden;
  }

  #wiersz-page .envelope:before {
	content: "";
	position: absolute;
	top: -20px;
	left: -20px;
	width: 100px;
	height: 100px;
	background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100"><text x="0" y="70" font-size="70" fill="%23e63946" opacity="0.2">&#9829;</text></svg>') no-repeat;
	background-size: contain;
  }

  /* Password container */
  #wiersz-page .password-container {
	margin-bottom: 20px;
	position: relative;
	display: inline-block;
  }

  /* Password input: type "password" with hidden text and caret */
  #wiersz-page input[type="password"] {
	font-size: 1.2em;
	padding: 10px;
	border: 2px solid #e63946;
	border-radius: 5px;
	width: 100%;
	color: transparent;
	caret-color: transparent;
	background: transparent;
	user-select: none;
	box-sizing: border-box;
  }

  #password-input::selection {
	color: transparent;
  }

  #wiersz-page .heart-mask {
	position: absolute;
	top: 0;
	left: 0;
	width: 300px;
	height: 100%;
	pointer-events: none;
	font-size: 1.2em;
	padding: 10px;
	color: #e63946;
	user-select: none;
  }

  /* Love letter area: the letter box fills remaining space within the envelope,
	 has a fixed margin at the top, and scrolls vertically if content overflows */
  #wiersz-page pre {
	flex: 1;
	text-align: left;
	background: #fff;
	padding: 18px;
	border-radius: 5px;
	white-space: pre-wrap;
	word-wrap: break-word;
	border: 1px solid #e63946;
	width: 100%;
	overflow-y: auto;
	box-sizing: border-box;
	margin-top: 20px;
  }

  /* Responsive Adjustments */
  @media (max-width: 600px) {
	#valentine-page h1, #wiersz-page h2 {
	  font-size: 1.8em;
	}
	.tile {
	  padding: 40px 60px;
	  font-size: 1.2em;
	}
	#wiersz-page input[type="password"],
	#wiersz-page .heart-mask {
		width: 100%;
		box-sizing: border-box;
		overflow: hidden;
	}
  }
#app{
	padding: 0;
	margin: 0;
}

::placeholder {
	text-align: center;
	font-size: 16px;
	color: #d36e76;
  }
